.Bulb:hover{
    /* opacity: 80%; */
    transition: ease-in-out;
    transition-duration: 0.2s;
}

.Bulb:active{
    opacity: 70%;
    transition: ease-in-out;
    transition-duration: 0.2s;
}

.fadeIn {
    animation: fade-in 0.8s cubic-bezier(0.250, 0.460, 0.450, 0.940) forwards;
  }
  
  @keyframes fade-in {
    0% {
      opacity: 0;
    }
    100% {   
      opacity: 1;
    }
}